<template>
  <div>
    <el-button @click="visible=true"></el-button>
    <!--   -->
    <el-dialog title="" :visible.sync="visible">
      <span></span>
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirm"> </el-button>
        <el-button @click="visible=false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import $http from '@/utils/http.js';
export default {
  name: 'activity-delete',
  props: ['activityId'],
  data() {
    return {
      visible: false
    };
  },
  computed: {
    unionId() {
      return this.$store.state.unionId;
    }
  },
  methods: {
    confirm() {
      $http
        .del(`/unionCardActivity/${this.activityId}/unionId/${this.unionId}`)
        .then(res => {
          if (res.data.success) {
            this.$message({ showClose: true, message: '', type: 'success', duration: 5000 });
            eventBus.$emit('activityDelete')
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    }
  }
};
</script>



